import { createRouter, createWebHashHistory } from "vue-router";
import StartScreen from "@/views/StartScreen.vue";
import PlayScreen from "@/views/PlayScreen";
import SettingScreen from "@/views/SettingScreen";
import NotFoundScreen from "@/views/NotfoundScreen.vue";
import WordQuizScreen from "@/views/WordQuiz.vue";
import AuthScreen from "@/views/AuthScreen.vue";
import { getAuth, onAuthStateChanged } from "firebase/auth";

const routes = [
	{
		path: "/",
		name: "Start",
		component: StartScreen,
	},
	{
		path: "/play",
		name: "Play",
		component: PlayScreen,
	},
	{
		path: "/auth",
		name: "Auth",
		component: AuthScreen,
	},
	{
		path: "/settings",
		name: "Settings",
		component: SettingScreen,
		meta: { requiresAuth: true }, // ‰øÆÊ≠£: requiredAuth ‚Üí requiresAuth „Å´Áµ±‰∏Ä
	},
	{
		path: "/wordQuiz",
		name: "WordQuiz",
		component: WordQuizScreen,
	},
	{
		path: "/:notFound(.*)",
		name: "error.404",
		component: NotFoundScreen,
	},

	// ‰ªñ„ÅÆ„É´„Éº„Éà„Çí„Åì„Åì„Å´ËøΩÂä†„Åó„Å¶„ÅÑ„Åç„Åæ„Åô
];

const router = createRouter({
	history: createWebHashHistory(),
	// history: createWebHashHistory(process.env.BASE_URL),
	routes,
});

// „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„Ç¨„Éº„Éâ - Ë™çË®º„ÅåÂøÖË¶Å„Å™„É´„Éº„Éà„Çí‰øùË≠∑
router.beforeEach((to, from, next) => {
	const auth = getAuth();
	const requiresAuth = to.matched.some((record) => record.meta.requiresAuth);

	if (requiresAuth) {
		// Ë™çË®º„ÅåÂøÖË¶Å„Å™„É´„Éº„Éà„Å∏„ÅÆ„Ç¢„ÇØ„Çª„ÇπÊôÇ„Å´Ë™çË®ºÁä∂ÊÖã„ÇíÁ¢∫Ë™ç
		onAuthStateChanged(auth, (user) => {
			if (user) {
				// „É¶„Éº„Ç∂„Éº„ÅåË™çË®ºÊ∏à„Åø„ÅÆÂ†¥Âêà„ÄÅ„Ç¢„ÇØ„Çª„Çπ„ÇíË®±ÂèØ
				next();
			} else {
				// Êú™Ë™çË®º„ÅÆÂ†¥Âêà„ÄÅË™çË®º„Éö„Éº„Ç∏„Å´„É™„ÉÄ„Ç§„É¨„ÇØ„Éà
				next({
					path: "/auth",
					query: { redirect: to.fullPath }, // „É™„ÉÄ„Ç§„É¨„ÇØ„ÉàÂÖà„Çí‰øùÂ≠ò
				});
			}
		});
	} else {
		// Ë™çË®º„Åå‰∏çË¶Å„Å™„É´„Éº„Éà„ÅØÈÄöÂ∏∏ÈÄö„ÇäÈÄ≤„ÇÄ
		next();
	}
});

export default router;
