<template>
  <v-container>
    <h1 class="mb-4">Ë®≠ÂÆöÁîªÈù¢</h1>

    <!-- „Éï„Ç°„Ç§„É´„Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÈÉ®ÂàÜ -->
    <v-file-input v-model="file" label="CSV„Éï„Ç°„Ç§„É´„Çí„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ" accept=".csv" @change="handleFileUpload"
      class="mb-4" ></v-file-input>

    <!-- ÂçòÂÖÉÈÅ∏ÊäûÈÉ®ÂàÜ -->
    <div v-if="!gameStarted" class="mb-6">
      <v-select v-model="selectedLesson" :items="lessons" item-title="name" item-value="id" label="„Åæ„Åü„ÅØÔºåÂçòÂÖÉ„ÇíÈÅ∏Êäû„Åó„Å¶Á∑®ÈõÜ"
        variant="outlined" class="mb-4" @update:model-value="onLessonSelect"></v-select>
    </div>

    <!-- „Ç≥„É≥„ÉûÂå∫Âàá„Çä„Åã„Çπ„Éö„Éº„ÇπÂå∫Âàá„Çä„ÇíÈÅ∏Êäû -->
    <v-btn-toggle v-model="selectedDelimiter" class="mb-4" color="primary">
      <v-btn value="comma" variant="tonal" @change="handleFileUpload">„Ç≥„É≥„ÉûÂå∫Âàá„Çä</v-btn>
      <v-btn value="space" variant="tonal" @change="handleFileUpload">„Çπ„Éö„Éº„ÇπÂå∫Âàá„Çä</v-btn>
    </v-btn-toggle>

    <!-- „Éï„Ç°„Ç§„É´„ÅÆÂÜÖÂÆπÁ¢∫Ë™ç„ÉªÁ∑®ÈõÜÈÉ®ÂàÜ -->
    <v-card v-if="sentences.length > 0" class="mb-4">
      <v-card-title>
        <v-text-field v-model="fileName" label="ÂçòÂÖÉÂêç„ÇíÂÖ•Âäõ" placeholder="‰æã: Unit1-Daily Life" class="mb-2"
          density="compact"></v-text-field>
      </v-card-title>

      <v-card-text>
        <div class="sentences-list">
          <div v-for="(sentence, index) in sentences" :key="index" class="sentence-item mb-4">
            <div class="d-flex align-center">
              <span class="mr-2">ÂïèÈ°å {{ index + 1 }}:</span>
              <v-text-field v-model="sentences[index]" density="compact" hide-details
                class="flex-grow-1"></v-text-field>
              <v-btn icon variant="text" color="error" size="large" class="ml-2" @click="removeSentence(index)">
                <v-icon>mdi-delete</v-icon>
              </v-btn>
            </div>

            <!-- „Éó„É¨„Éì„É•„ÉºË°®Á§∫ -->
            <div class="preview-chips ml-8 mt-2">
              <v-chip v-for="(word, wordIndex) in sentence.split(this.selectedDelimiter === 'comma' ? ',' : ' ')" :key="wordIndex" size="small" class="ma-1"
                :style="getChipStyle(word.trim(), index)" variant="tonal" @click="changeChipColor(word.trim(), index)">
                {{ word.trim() }}
              </v-chip>
            </div>
          </div>
        </div>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions class="overflow-x-auto d-flex" style="min-width: 0">
        <div class="d-flex align-center" style="gap: 8px">
          <v-btn prepend-icon="mdi-plus" variant="tonal" @click="addNewSentence">
            Êñ∞„Åó„ÅÑÂïèÈ°å„ÇíËøΩÂä†
          </v-btn>

          <v-btn prepend-icon="mdi-content-cut" variant="tonal" @click="extractSVOC"
            :loading="isProcessing"
            :disabled="isProcessing || !isValidData">
            ÂìÅË©ûÂàÜËß£
          </v-btn>

          <v-spacer></v-spacer>

          <v-btn prepend-icon="mdi-close" variant="tonal" color="error" @click="onCancelHandle">
            „Ç≠„É£„É≥„Çª„É´
          </v-btn>

          <v-btn prepend-icon="mdi-check" variant="tonal" color="primary"
            @click="saveSentences"
            :disabled="!isValidData || isProcessing">
            {{currentDataType==='lesson' ? "Êõ¥Êñ∞" : "‰øùÂ≠ò"}}
          </v-btn>

          <v-btn prepend-icon="mdi-delete" variant="tonal" color="error" @click="showDeleteConfirmation"
            :disabled="!isValidData || isProcessing || currentDataType !== 'lesson'">
            ÂâäÈô§
          </v-btn>

        </div>
      </v-card-actions>
    </v-card>

    <!-- „É°„ÉÉ„Çª„Éº„Ç∏Ë°®Á§∫ -->
    <v-alert v-if="message" :type="messageType" class="mt-4" closable @click:close="clearMessage">
      {{ message }}
    </v-alert>
    </v-container>
    <v-btn class="floating-btn" color="primary" icon="mdi-home" size="x-large"
        @click="$router.push('/')"></v-btn>

    <iframe src="https://docs.google.com/forms/d/e/1FAIpQLSdCcV_x_SOWFR_DyXy017G9ah1YFzhiOhrUo9V5F7n244OlrA/viewform?embedded=true" width="100%" height="702" frameborder="0" marginheight="0" marginwidth="0">Ë™≠„ÅøËæº„Çì„Åß„ÅÑ„Åæ„Åô‚Ä¶</iframe>

    <!-- ÂâäÈô§Á¢∫Ë™ç„ÉÄ„Ç§„Ç¢„É≠„Ç∞ -->
    <v-dialog v-model="deleteDialog" max-width="500px">
      <v-card>
        <v-card-title>Á¢∫Ë™ç</v-card-title>
        <v-card-text>
          „Äå{{ originalFileName || fileName }}„Äç„ÇíÂâäÈô§„Åó„Å¶„ÇÇ„Çà„Çç„Åó„ÅÑ„Åß„Åô„ÅãÔºü<br>
          <small class="text-grey">„Åì„ÅÆÊìç‰Ωú„ÅØÂèñ„ÇäÊ∂à„Åõ„Åæ„Åõ„Çì„ÄÇ</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" variant="text" @click="deleteDialog = false">„Ç≠„É£„É≥„Çª„É´</v-btn>
          <v-btn color="error" variant="text" @click="removeData" :loading="isProcessing">ÂâäÈô§„Åô„Çã</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- ‰øùÂ≠òÊàêÂäü„ÉÄ„Ç§„Ç¢„É≠„Ç∞ -->
    <v-dialog v-model="successDialog" max-width="500px">
      <v-card>
        <v-card-title>ÊàêÂäü</v-card-title>
        <v-card-text>
          {{ successMessage }}
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" variant="text" @click="closeSuccessDialog">OK</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>

<script>
import { db } from '../firebase/init'
import { collection, addDoc, query, getDocs, where, updateDoc, doc, deleteDoc } from 'firebase/firestore'
import axios from 'axios'

export default {
  name: 'SettingsScreen',
  data() {
    return {
      currentDataType: null,
      selectedLessonId: null, // ÈÅ∏Êäû„Åï„Çå„Åü„É¨„ÉÉ„Çπ„É≥„ÅÆID
      originalFileName: null, // ÂÖÉ„ÅÆ„Éï„Ç°„Ç§„É´ÂêçÔºàÊõ¥Êñ∞ÊôÇ„ÅÆË≠òÂà•Áî®Ôºâ
      lessons: [],
      selectedLesson: null,
      selectedDelimiter: 'comma', // „Éá„Éï„Ç©„É´„Éà„ÅØ„Ç≥„É≥„ÉûÂå∫Âàá„Çä
      file: null,
      sentences: [], // ÂÖÉ„ÅÆÊñáÁ´†Ôºà„Ç´„É≥„ÉûÂå∫Âàá„ÇäÔºâ
      analyzedSentences: [], // Ëß£ÊûêÁµêÊûú„Çí‰øùÊåÅ
      wordColors: [], // ÂêÑÂçòË™û„ÅÆËâ≤ÊÉÖÂ†±„Çí‰øùÊåÅ
      message: '',
      messageType: 'info',
      fileName: '',
      isProcessing: false,
      deleteDialog: false,  // ÂâäÈô§Á¢∫Ë™ç„ÉÄ„Ç§„Ç¢„É≠„Ç∞
      successDialog: false, // ‰øùÂ≠òÊàêÂäü„ÉÄ„Ç§„Ç¢„É≠„Ç∞
      successMessage: '',   // ÊàêÂäü„É°„ÉÉ„Çª„Éº„Ç∏
      colorScheme: {
        subject: '#FFEB3B', // ÈªÑËâ≤
        verb: '#A5D6A7', // Á∑ë
        object: '#90CAF9', // Èùí
        complement: '#FF9800', // „Ç™„É¨„É≥„Ç∏
        other: '#E0E0E0' // „Åù„ÅÆ‰ªñÔºà„Ç∞„É¨„ÉºÔºâ
      },
      apiConfig: {
        baseURL: 'https://english-app-serverside.onrender.com',
        timeout: 500000000,
        headers: {
          'Content-Type': 'application/json',
          // CORS„Éó„É™„Éï„É©„Ç§„Éà„É™„ÇØ„Ç®„Çπ„Éà„ÅÆ„Åü„ÇÅ„ÅÆËøΩÂä†„Éò„ÉÉ„ÉÄ„Éº
          'Accept': 'application/json'
        }
      }
    }
  },
  computed: {
    isValidData() {
      return this.fileName.trim() !== '' &&
        this.sentences.length > 0 &&
        this.sentences.every(s => s.trim() !== '')
    },
  },

  async mounted() {
    await this.loadLessons()
  },

  methods: {
    clearMessage() {
      this.message = '';
    },

    closeSuccessDialog() {
      this.successDialog = false;
      this.resetForm();
    },

    resetForm() {
      this.sentences = [];
      this.wordColors = [];
      this.fileName = '';
      this.originalFileName = null;
      this.selectedLessonId = null;
      this.analyzedSentences = [];
      this.currentDataType = null;
      this.selectedLesson = null;
      this.file = null;
    },

    showDeleteConfirmation() {
      this.deleteDialog = true;
    },

    async loadLessons() {
      try {
        const lessonsRef = collection(db, 'lessons')
        const querySnapshot = await getDocs(lessonsRef)
        this.lessons = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }))
      } catch (error) {
        this.showMessage('ÂçòÂÖÉ„ÅÆË™≠„ÅøËæº„Åø‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü', 'error')
      }
    },

    onLessonSelect() {
      const lesson = this.lessons.find(l => l.id === this.selectedLesson)
      this.currentDataType = 'lesson'
      if (lesson) {
        this.selectedLessonId = lesson.id;
        this.sentences = [...lesson.sentences]
        this.wordColors = [...lesson.wordColors]
        this.fileName = lesson.name
        this.originalFileName = lesson.name // ÂÖÉ„ÅÆÂêçÂâç„Çí‰øùÂ≠ò
        this.selectedDelimiter = lesson.delimiter || 'comma'
        this.analyzedSentences = lesson.analyzedSentences || []
      }
    },

    onCancelHandle(){
      this.resetForm();
    },

    getWordType(word, analysis) {
      if (analysis.subject.includes(word)) return 'subject'
      if (analysis.verb.includes(word)) return 'verb'
      if (analysis.object.includes(word)) return 'object'
      if (analysis.complement.includes(word)) return 'complement'
      return 'other'
    },
    getWordColor(word, index) {
      return this.wordColors[index]?.[word] || this.colorScheme.other
    },

    getChipStyle(word, sentenceIndex) {
      const backgroundColor = this.wordColors[sentenceIndex]?.[word] || this.colorScheme.other;
      // „Éê„ÉÉ„ÇØ„Ç∞„É©„Ç¶„É≥„Éâ„ÇíËñÑ„Åè„ÄÅ„ÉÜ„Ç≠„Çπ„Éà„ÇíÊøÉ„Åè
      return {
        backgroundColor: backgroundColor,
        color: this.getContrastColor(backgroundColor),
        borderColor: backgroundColor,
      }
    },

    changeChipColor(word, sentenceIndex) {
      // ÂÆâÂÖ®„Å´ÂàùÊúüÂåñ
      if (!this.wordColors[sentenceIndex]) {
        this.wordColors[sentenceIndex] = {};
      }

      // ÁèæÂú®„ÅÆËâ≤„ÇíÂèñÂæóÔºàË¶ã„Å§„Åã„Çâ„Å™„ÅÑÂ†¥Âêà„ÅØ "other" „ÅÆËâ≤„Çí‰Ωø„ÅÜÔºâ
      const currentValue = this.wordColors[sentenceIndex]?.[word] || this.colorScheme.other;

      // ÁèæÂú®„ÅÆËâ≤„Å´ÂØæÂøú„Åô„Çã„Ç≠„Éº„Çí„Åô„Åπ„Å¶ÂèñÂæóÔºàÈáçË§áËâ≤„Å´„ÇÇÂØæÂøúÔºâ
      const matchingKeys = Object.keys(this.colorScheme).filter(
        (key) => this.colorScheme[key] === currentValue
      );

      // Ë§áÊï∞„ÅÆ„Ç≠„Éº„ÅåË¶ã„Å§„Åã„Å£„ÅüÂ†¥Âêà„ÅØ„ÄÅ„Åù„ÅÆ‰∏≠„ÅÆ1„Å§„ÇíÈÅ∏„Å∂ÔºàÂÖàÈ†≠„Çí‰ΩøÁî®Ôºâ
      const currentKey = matchingKeys[0];

      const keys = Object.keys(this.colorScheme); // ÂÖ®„Å¶„ÅÆ„Ç≠„Éº„ÇíÂèñÂæó
      const currentIndex = keys.indexOf(currentKey); // ÁèæÂú®„ÅÆ„Ç§„É≥„Éá„ÉÉ„ÇØ„Çπ„ÇíÂèñÂæó

      // Ê¨°„ÅÆ„Ç§„É≥„Éá„ÉÉ„ÇØ„Çπ„ÇíË®àÁÆóÔºàÂæ™Áí∞„Åô„Çã„Çà„ÅÜ„Å´Ôºâ
      const nextIndex = (currentIndex + 1) % keys.length;

      // Ê¨°„ÅÆËâ≤„ÇíË®≠ÂÆö
      this.wordColors[sentenceIndex][word] = this.colorScheme[keys[nextIndex]];

    },

    getContrastColor(hexColor) {
      // „Ç´„É©„Éº„Ç≥„Éº„Éâ„ÇíRGB„Å´Â§âÊèõ
      const r = parseInt(hexColor.slice(1, 3), 16);
      const g = parseInt(hexColor.slice(3, 5), 16);
      const b = parseInt(hexColor.slice(5, 7), 16);

      // ËºùÂ∫¶„ÇíË®àÁÆó
      const brightness = (r * 299 + g * 587 + b * 114) / 1000;

      // Êòé„Çã„ÅÑËÉåÊôØ„Å´„ÅØÊöó„ÅÑ„ÉÜ„Ç≠„Çπ„Éà„ÄÅÊöó„ÅÑËÉåÊôØ„Å´„ÅØÊòé„Çã„ÅÑ„ÉÜ„Ç≠„Çπ„Éà
      return brightness > 128 ? '#000000' : '#FFFFFF';
    },

    handleFileUpload() {
      if (this.file) {
        this.currentDataType = 'file'
        this.selectedLessonId = null; // „Éï„Ç°„Ç§„É´„Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÊôÇ„ÅØID„Çí„ÇØ„É™„Ç¢
        this.originalFileName = null;
        const reader = new FileReader()
        reader.onload = (e) => {
          const content = e.target.result
          this.sentences = content.split('\n')
            .filter(line => line.trim() !== '')
            .map(line => line.trim())
          // „Éï„Ç°„Ç§„É´Âêç„Åã„ÇâÊã°ÂºµÂ≠ê„ÇíÈô§„ÅÑ„Å¶ÂçòÂÖÉÂêç„ÅÆÂàùÊúüÂÄ§„Å®„Åó„Å¶Ë®≠ÂÆö
          if (this.file.name) {
            this.fileName = this.file.name.replace(/\.(csv|txt)$/i, '');
          }
        }
        reader.readAsText(this.file)
      }
    },

    addNewSentence() {
      this.sentences.push('')
    },

    removeSentence(index) {
      this.sentences.splice(index, 1)
      // ÂØæÂøú„Åô„ÇãËâ≤ÊÉÖÂ†±„ÇÇÂâäÈô§
      if (this.wordColors[index]) {
        this.wordColors.splice(index, 1)
      }
      if (this.analyzedSentences[index]) {
        this.analyzedSentences.splice(index, 1)
      }
    },

    async removeData() {
      if (this.currentDataType !== 'lesson' || !this.selectedLessonId) {
        this.showMessage('ÂâäÈô§ÂØæË±°„ÅÆÂçòÂÖÉ„ÅåÈÅ∏Êäû„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì„ÄÇ', 'error');
        return;
      }

      this.isProcessing = true;
      this.deleteDialog = false;
      this.showMessage(`„Éá„Éº„Çø„ÇíÂâäÈô§‰∏≠„Åß„Åô...${this.originalFileName || this.fileName}`, 'info');

      try {
        // „Éâ„Ç≠„É•„É°„É≥„ÉàID„Çí‰Ωø„Å£„Å¶Áõ¥Êé•ÂâäÈô§
        const docRef = doc(db, 'lessons', this.selectedLessonId);
        await deleteDoc(docRef);

        // ÊàêÂäü„ÉÄ„Ç§„Ç¢„É≠„Ç∞„ÇíË°®Á§∫
        this.successMessage = '„Éá„Éº„Çø„ÅåÊ≠£Â∏∏„Å´ÂâäÈô§„Åï„Çå„Åæ„Åó„Åü„ÄÇ';
        this.successDialog = true;

        this.showMessage('„Éá„Éº„Çø„ÅåÊ≠£Â∏∏„Å´ÂâäÈô§„Åï„Çå„Åæ„Åó„Åü„ÄÇ', 'success');
        // „É¨„ÉÉ„Çπ„É≥„É™„Çπ„Éà„ÇíÂÜçË™≠„ÅøËæº„Åø
        await this.loadLessons();

      } catch (error) {
        console.error("ÂâäÈô§‰∏≠„Å´„Ç®„É©„Éº:", error);
        this.showMessage(`ÂâäÈô§‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü: ${error.message}`, 'error');
      } finally {
        this.isProcessing = false;
      }
    },

    // ÂêçÂâç„ÅÆÈáçË§á„ÉÅ„Çß„ÉÉ„ÇØ
    async checkNameConflict(newName) {
      // Êó¢Â≠ò„É¨„ÉÉ„Çπ„É≥„ÅÆÊõ¥Êñ∞„ÅÆÂ†¥Âêà„ÄÅËá™ÂàÜËá™Ë∫´„ÅØÈô§Â§ñ
      if (this.currentDataType === 'lesson' && this.originalFileName === newName) {
        return false; // ÂêçÂâç„ÅåÂ§âÊõ¥„Åï„Çå„Å¶„ÅÑ„Å™„ÅÑÂ†¥Âêà„ÅØÁ´∂Âêà„Å™„Åó
      }

      const lessonsRef = collection(db, 'lessons');
      const q = query(lessonsRef, where("name", "==", newName));
      const querySnapshot = await getDocs(q);

      return !querySnapshot.empty;
    },

    validateSentence(sentence) {
      // Âå∫Âàá„Çä„ÅÆÂΩ¢Âºè„ÅåÊ≠£„Åó„ÅÑ„Åã„ÉÅ„Çß„ÉÉ„ÇØ
      const words = sentence.split(this.selectedDelimiter === 'comma' ? ',' : ' ')
      return words.length >= 2 && words.every(word => word.trim() !== '')
    },

    async extractSVOC() {
      if (!this.isValidData) {
        this.showMessage('ÂçòÂÖÉÂêç„Å®Â∞ë„Å™„Åè„Å®„ÇÇ1„Å§„ÅÆÊúâÂäπ„Å™ÂïèÈ°å„ÅåÂøÖË¶Å„Åß„Åô„ÄÇ', 'error')
        return
      }

      this.isProcessing = true
      this.showMessage('ÊñáÁ´†„ÇíËß£Êûê‰∏≠„Åß„Åô...', 'info')

      try {
        this.analyzedSentences = []
        this.wordColors = []

        const api = axios.create({
          baseURL: 'https://english-app-serverside.onrender.com',
          headers: {
            'Content-Type': 'application/json'
          },
          // render„ÅÆ„Çµ„Éº„Éê„Éº„ÅØÂøúÁ≠î„ÅåÈÅÖ„ÅÑ„Åü„ÇÅ„ÄÅ„Çø„Ç§„É†„Ç¢„Ç¶„Éà„ÇíÈï∑„ÇÅ„Å´Ë®≠ÂÆö
          timeout: 30000
        })

        for (let i = 0; i < this.sentences.length; i++) {
          const sentence = this.sentences[i]
          const words = sentence.split(this.selectedDelimiter === 'comma' ? ',' : ' ')
          const wholeSentence = words.join(' ').trim()
          if (!wholeSentence) continue

          try {
            const response = await api.post('/analyze', {
              text: wholeSentence
            })

            if (response.data) {
              this.analyzedSentences[i] = response.data
              const colors = {}
              words.forEach(word => {
                const cleanWord = word.trim()
                const wordType = this.getWordType(cleanWord, response.data)
                colors[cleanWord] = this.colorScheme[wordType]
              })
              this.wordColors[i] = colors
            }

          } catch (error) {
            if (axios.isAxiosError(error)) {
              if (error.code === 'ECONNABORTED') {
                throw new Error('„Çµ„Éº„Éê„Éº„ÅÆÂøúÁ≠î„Åå„Çø„Ç§„É†„Ç¢„Ç¶„Éà„Åó„Åæ„Åó„Åü„ÄÇ„Åó„Å∞„Çâ„ÅèÂæÖ„Å£„Å¶„Åã„ÇâÂÜçË©¶Ë°å„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ')
              }
              if (error.response) {
                throw new Error(`„Çµ„Éº„Éê„Éº„Ç®„É©„Éº: ${error.response.status} - ${error.response.data?.error || '‰∏çÊòé„Å™„Ç®„É©„Éº'}`)
              }
              if (error.request) {
                throw new Error('„Çµ„Éº„Éê„Éº„Å´Êé•Á∂ö„Åß„Åç„Åæ„Åõ„Çì„ÄÇ„Ç§„É≥„Çø„Éº„Éç„ÉÉ„ÉàÊé•Á∂ö„ÇíÁ¢∫Ë™ç„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ')
              }
            }
            throw error
          }
        }

        this.showMessage('Ëß£Êûê„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„Åü„ÄÇ', 'success')
      } catch (error) {
        this.showMessage(`Ëß£Êûê‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü: ${error.message}`, 'error')
      } finally {
        this.isProcessing = false
      }
    },

    async saveSentences() {
      if (!this.isValidData) {
        this.showMessage('ÂçòÂÖÉÂêç„Å®Â∞ë„Å™„Åè„Å®„ÇÇ1„Å§„ÅÆÊúâÂäπ„Å™ÂïèÈ°å„ÅåÂøÖË¶Å„Åß„Åô„ÄÇ', 'error')
        return
      }

      this.isProcessing = true
      this.showMessage('ÊñáÁ´†„Çí‰øùÂ≠ò‰∏≠„Åß„Åô...', 'info')

      try {
        // ÂêçÂâç„ÅÆÈáçË§á„ÉÅ„Çß„ÉÉ„ÇØ
        const hasConflict = await this.checkNameConflict(this.fileName);
        if (hasConflict) {
          this.showMessage('Âêå„ÅòÂêçÂâç„ÅÆÂçòÂÖÉ„ÅåÊó¢„Å´Â≠òÂú®„Åó„Åæ„Åô„ÄÇÂà•„ÅÆÂêçÂâç„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ', 'error');
          return;
        }

        const lessonData = {
          name: this.fileName,
          sentences: this.sentences,
          delimiter: this.selectedDelimiter,
          analyzedSentences: this.analyzedSentences,
          wordColors: this.wordColors,
          updatedAt: new Date().toISOString()
        }

        if (this.currentDataType === 'lesson' && this.selectedLessonId) {
          // Êó¢Â≠ò„É¨„ÉÉ„Çπ„É≥„ÅÆÊõ¥Êñ∞
          const docRef = doc(db, 'lessons', this.selectedLessonId);
          await updateDoc(docRef, lessonData);
          this.successMessage = 'ÂçòÂÖÉ„ÅåÊ≠£Â∏∏„Å´Êõ¥Êñ∞„Åï„Çå„Åæ„Åó„Åü„ÄÇ';
        } else {
          // Êñ∞Ë¶è„É¨„ÉÉ„Çπ„É≥„ÅÆ‰ΩúÊàê
          lessonData.createdAt = new Date().toISOString();
          const lessonsRef = collection(db, 'lessons');
          await addDoc(lessonsRef, lessonData);
          this.successMessage = 'ÂçòÂÖÉ„ÅåÊ≠£Â∏∏„Å´‰øùÂ≠ò„Åï„Çå„Åæ„Åó„Åü„ÄÇ';
        }

        // ÊàêÂäü„ÉÄ„Ç§„Ç¢„É≠„Ç∞„ÇíË°®Á§∫
        this.successDialog = true;

        this.showMessage('ÊñáÁ´†„ÅåÊ≠£Â∏∏„Å´‰øùÂ≠ò„Åï„Çå„Åæ„Åó„Åü„ÄÇ', 'success');

        // „É¨„ÉÉ„Çπ„É≥„É™„Çπ„Éà„ÇíÂÜçË™≠„ÅøËæº„Åø
        await this.loadLessons();

      } catch (error) {
        console.error("‰øùÂ≠ò‰∏≠„Å´„Ç®„É©„Éº:", error);
        this.showMessage(`‰øùÂ≠ò‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü: ${error.message}`, 'error');
      } finally {
        this.isProcessing = false;
      }
    },

    showMessage(text, type = 'info') {
      this.message = text
      this.messageType = type
    },

    dragEnter(e) {
      console.log('dragEnter', e)
    }
  }
}
</script>

